/* Stile per la sezione dei progetti */
#projects {
  padding: 0px 0;
  text-align: center;
  background-color: #282c34; /* Sfondo principale della pagina */
}

.project-container {
  background-color: #282c34; /* Sfondo della sezione del titolo e descrizione */
}

.project-title {
  padding: 10px 20px;
  color: white; /* Colore testo bianco per contrastare con lo sfondo scuro */
  margin-bottom: 40px; /* Mantiene un margine inferiore per separare il titolo dai progetti */
  font-size: 2em;
}

.project-description {
  padding: 20px;
  color: white; /* Colore testo bianco */
  margin-bottom: 60px; /* Mantiene spazio sotto la descrizione */
  max-width: 50%;
  margin: 0 auto;
}

/* Nuova sezione con sfondo bianco per i progetti */
.project-list-container {
  background-color: white; /* Sfondo bianco per i progetti */
  padding: 50px 100px; /* Aumentato il padding per avvicinare i progetti al footer */
}

.project-list {
  display: flex;
  justify-content: center; /* Centra i progetti orizzontalmente */
  gap: 50px; /* Spazio tra i progetti */
  flex-wrap: wrap; /* Permette ai progetti di andare su una nuova riga se necessario */
}

.project-card {
  position: relative;
  width: 380px; /* Aumenta la larghezza per migliorare la visibilità dell'anteprima */
  border: 3px solid #ddd;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
  background-color: white;
  cursor: pointer;
  top: 0px;
}

.project-image {
  width: 100%;
  height: auto;
  display: block;
}

/* Dettagli aggiuntivi per ogni progetto */
.project-details {
  padding: 15px;
  color: #333;
  text-align: left;
}

.project-details h3 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.project-details p {
  font-size: 1em;
  margin-bottom: 20px;
}

/* Pulsante di azione */
.project-button {
  padding: 10px 20px;
  background-color: #61dafb;
  color: white;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease-out, transform 0.3s ease-out;
  width: 100%;
  text-align: center;
}

/* Animazione hover del pulsante */
.project-button:hover {
  background-color: #4da1c0;
  
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 800px) {
  .project-description {
    max-width: 100%;
  }

  .project-list {
    flex-direction: column;
    align-items: center;
  }

  .project-card {
    width: 90%; /* Aumenta la larghezza per adattarsi meglio agli schermi piccoli */
    max-width: 450px;
  }
}

/* Stile per schermi molto piccoli (max 500px) */
@media screen and (max-width: 500px) {
  .project-card {
    width: 100%;
    max-width: 100%; /* La card occupa tutta la larghezza disponibile */
    margin-bottom: 20px;
  }

  .project-details {
    padding: 10px;
    color: white;
    text-align: center;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8); /* Overlay più scuro */
    display: none; /* Nasconde il contenuto di default */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(5px); /* Sfocatura per un effetto più elegante */
    transition: transform 0.3s ease-out, opacity 0.3s ease-out;
  }

  /* Titolo e pulsante */
  .project-details h3 {
    font-size: 1em; /* Riduce la dimensione del titolo */
    margin-bottom: 10px;
  }

  .project-button {
    padding: 8px 0px;
    font-size: 0.8em;
    border-radius: 5px;
    width: 70%; /* Riduce la larghezza del pulsante */
  }

  .project-button:hover {
    transform: translateY(-3px) scale(1.02);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  }
}
